export default function GoodDetail(props:any) {
    const userLoginGlo = userLoginMsg();//登录信息
    if(!userLoginGlo.isLogin) {
        React.useEffect(() => {
            props.navigation.navigate('Login');
        }, []);
        
        return null;
    }
    return (
        <Text style={mallTypeMod.container}>
            商品详情--疯狂建设中
        </Text>
    );
}

